@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:ital,wght@0,200;0,900;1,300;1,600&display=swap);
html {
  display: flex;
  align-items: center;
  justify-content: center;
}

h1 {
  text-align: center;
}

:root {
  --color-bg: #ffc4005b;
  --color-bg-variant: rgba(1, 102, 21, 0.404);
  --color-primary: #21831e88;
  --color-primary-variant: rgb(7, 104, 173);
  --color-white: rgb(123, 143, 82);
  --color-light: rgb(24, 21, 3);
  --color-border: rgba(167, 185, 11, 0.616);

  --transition: all 400ms ease;

  --container-width-lg: 75%;
  --container-width-md: 86%;
  --container-width-sm: 90%;
}

body {
  padding: 1rem;
  /* font-family: "Poppins", sans-serif; */
  background: #ffc4005b;
  background: var(--color-bg);

  color: rgb(123, 143, 82);

  color: var(--color-white);
  line-height: 1.7;
  background-image: url(/makval/static/media/gifM.cb169199.gif);
  background-size: cover;
}

/* ============ Gen Styles =============== */

@media screen and (min-width: 800px) {
  header {
    display: flex;
    justify-content: center;
    align-items: center;
    max-width: 900px;
  }
  .container {
    /* border: solid 1rem red; */
    max-width: 800px;
  }

  h1,
  h2,
  h3,
  h4,
  h5 {
    font-weight: 500;
  }

  h1 {
    font-size: 2.5rem;
  }

  section {
    margin-top: 3%;
  }

  section > h2,
  section > h5 {
    text-align: center;
    color: rgb(24, 21, 3);
    color: var(--color-light);
  }

  section > h2 {
    color: #21831e88;
    color: var(--color-primary);
    margin-bottom: 3rem;
  }

  .text-light {
    color: rgb(24, 21, 3);
    color: var(--color-light);
  }

  a {
    color: #21831e88;
    color: var(--color-primary);
    transition: all 400ms ease;
    transition: var(--transition);
  }

  a:hover {
    color: rgb(123, 143, 82);
    color: var(--color-white);
  }

  img {
    max-width: 100%;
  }
}

header {
  /* border: tomato solid 1rem; */
  background-color: rgba(1, 102, 22, 0.329);
  border-radius: 2rem;
  max-height: 120px;
  max-width: 1000px;
  display: flex;
  align-items: center;
  justify-content: center;
}

header h1 {
  color: rgba(112, 93, 8, 0.781);
  font-size: 3rem;
}

header h3 {
  color: rgba(112, 93, 8, 0.781);
  margin-bottom: -1.5rem;
  float: right;
}

header h5 {
  color: rgba(112, 93, 8, 0.781);
  margin-top: -2.9rem;
}

@media screen and (max-width: 800px) {
  header {
    max-width: 500px;
  }
}

@media screen and (max-width: 450px) {
  header {
    max-width: 300px;
  }
}

.about__container {
  display: flex;
  grid-gap: 3rem;
  gap: 3rem;
}
.about__me {
  max-width: 1000px;
  /* aspect-ratio: 1/1; */
  border-radius: 2rem;
  background: linear-gradient(
    45deg,
    transparent,
    var(--color-primary),
    transparent
  );
  display: grid;
  place-items: center;
}

.about__me-img {
  border-radius: 2rem;
  border: 3px solid transparent;
  overflow: hidden;
  -webkit-transform: rotate(15deg);
          transform: rotate(15deg);
  transition: var(--transition);
}

.about__me-img:hover {
  -webkit-transform: rotate(0deg);
          transform: rotate(0deg);
  border-color: var(--color-border);
}

.about__cards {
  display: flex;

  grid-gap: 1.5rem;

  gap: 1.5rem;
}

.about__card {
  background: var(--color-bg-variant);
  border: 3px solid transparent;
  border-radius: 1rem;
  padding: 0.5rem;

  display: flex;
  align-items: center;
  justify-content: center;
}

.about__card:hover {
  border-color: var(--color-border);
  border-radius: 2rem;
  transition: var(--transition);
}

.about__icon {
  margin-right: 0.5rem;

  color: var(--color-primary);
  font-size: 2.5rem;
  margin-bottom: 0.5rem;
}

.about__card h3 {
  font-size: 1rem;
}

.aboutTxt {
  padding-top: 1rem;
  max-width: 1000px;
  /* margin: 2rem; */
  color: var(--color-light);

  background-color: rgba(255, 248, 220, 0.308);

  border-radius: 1.5rem;
  padding-bottom: 10rem;
  /* border: 0.1rem solid white; */
  /* overflow-wrap: wrap; */
}

p {
  padding-left: 1rem;
  padding-right: 1rem;
  padding-bottom: 0.5rem;
  text-indent: 1rem;
}

.bullets {
  margin: 1rem;
}

.li__child {
  font-style: italic;
  margin-left: 1.5rem;
  list-style-type: "\2964   ";
  /* color: var(--color-primary); */
}

a {
  text-decoration: none;
  /* background-color: rgb(110, 143, 110); */
  border-radius: 1rem;
  padding: 0.5rem;
  color: rgb(250, 184, 4);
}

/* ============small device=============== */

