    padding-top: 2rem;
  }
  .about__container {
    display: grid;
    grid-gap: 2rem;
    gap: 2rem;
    max-width: 600px;
  }
  .about__me {
    margin-top: 1rem;
    max-width: 500px;
    aspect-ratio: 1/0.75;
    border-radius: 2rem;
    background: linear-gradient(
      45deg,
      transparent,
      var(--color-primary),
      transparent
    );
    display: grid;
    place-items: center;
  }

  img {
    border-radius: 1rem;
    max-width: 100%;
  }

  .about__me-img {
    /* border-radius: 2rem; */
    border-radius: 1rem;
