    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
    border-color: var(--color-border);
  }

  .about__cards {
    display: flex;
    grid-gap: 1.5rem;
    gap: 1.5rem;
  }

  .about__card {
    background: var(--color-bg-variant);
    border: 3px solid transparent;
    border-radius: 1rem;
    padding: 0.5rem;

    display: flex;
    align-items: center;
    justify-content: center;
  }

  .about__card:hover {
    border-color: var(--color-border);
    border-radius: 2rem;
    transition: var(--transition);
  }

  .about__icon {
    margin-right: 0.5rem;

    color: var(--color-primary);
    font-size: 2.5rem;
    margin-bottom: 0.5rem;
  }

  .about__card h3 {
    font-size: 1rem;
  }

  .aboutTxt {
    padding-top: 1rem;
    max-width: 1000px;
    /* margin: 1rem; */
    color: var(--color-light);

    background-color: rgba(255, 248, 220, 0.308);

    border-radius: 1.5rem;
    padding-bottom: 1rem;
  }

  p {
    padding-left: 1rem;
    padding-right: 1rem;
    padding-bottom: 0.5rem;
    text-indent: 1rem;
  }

  .bullets {
    margin: 1rem;
  }

  .li__child {
    font-style: italic;
    margin-left: 1.5rem;
    list-style-type: "\2964   ";
  }
}

@media screen and (max-width: 500px) {
  section {
    display: grid;
    padding-top: 1.5rem;
  }
  .about__container {
    display: grid;
    grid-gap: 2rem;
    gap: 2rem;
    max-width: 250px;
  }
  .about__me {
    max-width: 400px;
  }

  .about__cards {
    grid-gap: 0.5rem;
    gap: 0.5rem;
  }

  .about__card {
    padding: 0.3rem;
  }

  .aboutTxt {
    /* margin-left: 1rem; */
    padding-top: 1rem;
    /* max-width: 400px;
    min-width: 350px; */

    color: var(--color-light);

    background-color: rgba(255, 248, 220, 0.308);

    border-radius: 1.5rem;
    padding-bottom: 6rem;
  }

  p {
    padding-left: 0.3rem;
    padding-right: 0.2rem;
    padding-bottom: 0.2rem;
    text-indent: 0.5rem;
  }
}

@media screen and (max-width: 330px) {
  .about__cards {
    grid-gap: 0.5rem;
    gap: 0.5rem;
  }

  .about__card {
    padding: 0.3rem;
  }

  .aboutTxt {
    margin-right: -8rem;
  }

  p {
    padding-left: 0.3rem;
    padding-right: 0.2rem;
    padding-bottom: 0.2rem;
    text-indent: 0.5rem;
  }
}

.portfolio__container {
  width: 99%;
  display: flex;
  grid-gap: 2rem;
  gap: 2rem;
  padding-top: 1rem;
}

.portfolio__item {
  width: 33%;
}

article a {
  font-size: 85%;
  display: flex;
  grid-gap: 0.1rem;
  gap: 0.1rem;
}


nav {
  background: rgba(28, 235, 1, 0.144);

  padding: 0.7rem 1.7rem;
  position: fixed;
  display: flex;
  align-items: center;
  bottom: 1rem;
  /* gap: 0.8rem; */

  border-radius: 3rem;
  -webkit-backdrop-filter: blur(10px);
          backdrop-filter: blur(10px);
}

nav a {
  background: transparent;
  padding: 0.9rem;
  border-radius: 50%;
  /* display: flex; */
  color: var(--color-light);
  /* font-size: 1.9rem; */
}

nav a:hover {
  background: rgba(0, 0, 0, 0.3);
  font-size: 1.5rem;
}

nav a.active {
  background: var(--color-bg);
  color: var(--color-white);
}

@media screen and (max-width: 500px) {
  nav {
    bottom: 1rem;
  }
}

